.banner-overlay{
    background-color: black;
    opacity: 0.7;
    width: 40rem;
    height: 22rem;
    position: absolute;
    top: 10rem;
    left: 60%;
    border-radius: 1rem;
}
.close-banner-overlay >i{
    color: white;
    position: absolute;
    right: 1rem;
    top: 1rem;
}
.search{
    display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   height: 60%;
   /* border: 1px solid red; */
}
.search > input{
    width: 22rem;
    padding: 0.6rem;
    border-radius: 5px;
    border: 0;
    margin-bottom: 1rem;
}
.cta-buttons{
    color: white;
    display: flex;
    justify-content: space-around;
    font-size: 1.5rem;
    /* border: 1px solid red; */
    width: 40rem;
}
.search-parameters{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.select-buttons{
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 30rem;
}
select{
    font-size: 1.4rem;  
    border: 0;
    border-radius: 5px;
}
.texts{
    color: white;
    height: 40%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.close-banner-overlay > i{
    color: white;
    
}
.mini{
    height: 2.5rem;
    transition: 1s;
    width: 30rem;
}
.close{
    display: none;
}
.transition{
    transition: 1s;
}
.text-open {
    animation-duration: 2s;
    animation-name: text-open;
}
.text-close {
    animation-duration: 1.5s;
    animation-name: text-close;
    color: red;
}
@keyframes text-open {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes text-close {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}