.navbar-container {
    /* height: 5rem; */
    display: flex;
    align-items: center;
    padding: 2rem;
    z-index: 999;
    background-color: transparent;
    position: fixed;
    width: 100%;
}
.active{
    background-color: rgb(104, 104, 190);
}
.logo {
    margin-right: 2rem;
    color: white;
}
.logo:hover {
    color: wheat;
    cursor: pointer;
}

.nav-display {
    display: flex;
    flex: 1;
}

.nav-items {
    display: flex;
    justify-content: space-around;
    width: 30rem;
    align-items: center;
}

.user-login {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.seperator-bar{
 font-size: 2rem;
 font-weight: 100;
}

.menu-toggle {
    display: none;
}



@media screen and (max-width: 900px) {
    .navbar-container{
        height: 6.3rem;
    }
    .close {
        display: none;
    }
    
    .open {
        display: block;
    }
    .menu-toggle {
        display: block;
        position: absolute;
        right: 2rem;
    }

    .menu-toggle>i {
        font-size: 2rem;
        color: white;
    }
    .menu-toggle :hover {
        color: wheat;
        cursor: pointer;
    }

    .nav-display {
        position: absolute;
        top: 5.1rem;
        left: 0;
        background-color: rgb(113, 113, 214);
        width: 100vw;
        padding-right: 2rem;
        height: 16rem;
    }

    .nav-items {
        display: flex;
        flex-direction: column;
        padding-left: 2rem;
        height: 14rem;
    }
    .user-login{
        position: absolute;
        bottom: 0;
        padding-left: 2rem;
        padding-bottom: 0.5rem;
    }
}